.user{
  margin-bottom: 10px;
  display: flex;
  width: 250px;
}
.user img{
  height: 60px;
  border-radius: 10px;
}
.name{
  text-align: left;
  width: 100%;
  cursor: pointer;
  padding: 15px 0 0 20px;
  border-radius: 10px;
  background-color: cadetblue;
  letter-spacing: 1px;
  font-size: 18px;
  margin-left: 5px;
  border: 2px solid #42787a;
}
.active{
  color: darkorange;
}