.dialogs{
  display: grid;
  grid-template-columns: 1fr 2fr;
  padding-bottom: 20px;
}
.messages{
  position: relative;
}
.messages:before{
    content: "";
    left: -18px;
    top: 40px;
    width: 2px;
    height: 90%;
    position: absolute;
    background-color: #345f61;
    border-radius: 3px;
}