.messages{
  padding: 5px 0 0 20px;
}
.title{
  color: aliceblue;
  font-size: 18px;
  font-weight: 500;
  margin-bottom: 20px;
  text-align: center;
}
.messages_list{
  padding: 20px 50px 0;
  background-color: cadetblue;
  position: relative;
  margin-right: 35px;
  height: 400px;
  border-radius: 10px;
  overflow: overlay;
  border-bottom: 10px solid #42787a;
  border-top: 10px solid #42787a;
}
.messages_list::-webkit-scrollbar{
  display: none;
}